import React, {Component} from 'react';
import { Form, Input, Tooltip, Icon, Cascader, Select, Row, Col, Checkbox, Button,Modal,DatePicker,Radio,Slider } from 'antd';
import moment from 'moment';
const RadioGroup = Radio.Group;

const FormItem = Form.Item;
const Option = Select.Option;

const dateFormat = 'YYYY-MM-DD';


const residences = [{
  value: 'zhejiang',
  label: '',
  children: [{
    value: 'hangzhou',
    label: '',
    children: [{
      value: 'xihu',
      label: '',
    }],
  }],
}, {
  value: 'jiangsu',
  label: '',
  children: [{
    value: 'nanjing',
    label: '',
    children: [{
      value: 'zhonghuamen',
      label: '',
    }],
  }],
}];

/**
 * 
 */
function checkformSubmit(form){
    return form.getFieldValue("agreement") ? false: true;
}

class RegistrationForm extends React.Component {

  
  state = {
    confirmDirty: false,
    captchaUrl:'/api/commonProcessor/captcha?v='+new Date(),
    model:{
        sex:1,
        slider:0
    },
    visible:false
  };

  /**
   * 
   */
  showModal = (values) => {
    this.setState({
      visible: true,
      model:values
    });
  }

  /**
  * 
  */
  handleOk = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  /**
   * 
   */
  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  /**
   * 
   */
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        this.showModal(values);
      }
    });
  }


  handleConfirmBlur = (e) => {
    const value = e.target.value;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  }

  /**
   * 
   */
  checkPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue('password')) {
      callback('!');
    } else {
      callback();
    }
  }

  checkConfirm = (rule, value, callback) => {
    const form = this.props.form;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  }

 /**
  * 
  */
  formReset = (e) =>{
       this.props.form.resetFields();
  }

  /**
   * 
   */
  setFormData = (e) =>{
      this.props.form.setFieldsValue({
            email:'demo@viewhigh.com',
            password:'demo',
            confirm:'demo',
            nickname:'demo',
            phone:'18600000000',
            birthday:moment('2017-06-30',dateFormat),
            captcha:'test'
      });
  }

  /**
   * 
   */
  changeCaptcha = (e) =>{
      this.setState({captchaUrl:'/api/commonProcessor/captcha?v='+new Date()+Math.round(200)});
  }

  render() {
    const { getFieldDecorator,getFieldValue } = this.props.form;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 14,
          offset: 6,
        },
      },
    };
    const prefixSelector = getFieldDecorator('prefix', {
        initialValue: '86',
    })(
      <Select className="icp-selector">
        <Option value="86">+86</Option>
      </Select>
    );
    return <div>
       <Modal title="" visible={this.state.visible}
          onOk={this.handleOk} onCancel={this.handleCancel}
        >
         { JSON.stringify(this.state.model) }
      </Modal>
      <Form onSubmit={this.handleSubmit}>
        <FormItem
          {...formItemLayout}
          label=""
          hasFeedback
        >
          {getFieldDecorator('email', {
            rules: [{
              type: 'email', message: '!',
            }, {
              required: true, message: '!',
            }],
          })(
            <Input  />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
          hasFeedback
        >
          {getFieldDecorator('password', {
            rules: [{
              required: true, message: '!',
            }, {
              validator: this.checkConfirm,
            }],
          })(
            <Input type="password" />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
          hasFeedback
        >
          {getFieldDecorator('confirm', {
            rules: [{
              required: true, message: '!',
            }, {
              validator: this.checkPassword,
            }],
          })(
            <Input type="password" onBlur={this.handleConfirmBlur} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label={(
            <span>
              &nbsp;
              <Tooltip title="?">
                <Icon type="question-circle-o" />
              </Tooltip>
            </span>
          )}
          hasFeedback
        >
          {getFieldDecorator('nickname', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('residence', {
            initialValue: ['zhejiang', 'hangzhou', 'xihu'],
            rules: [{ type: 'array', required: true, message: '!' }],
          })(
            <Cascader options={residences} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('phone', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input addonBefore={prefixSelector} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
          hasFeedback
        >
          {getFieldDecorator('birthday', {
            rules: [{ type: 'object', required: true, message: '!' }]
          })(
             <DatePicker format={dateFormat} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('sex', {
            initialValue:this.state.model.sex
          })(
            <RadioGroup >
                <Radio value={0}></Radio>
                <Radio value={1}></Radio>
            </RadioGroup>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('slider',{
              initialValue:this.state.model.slider
          })(
            <Slider marks={{ 0: 'A', 20: 'B', 40: 'C', 60: 'D', 80: 'E', 100: 'F' }} />
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          <Row gutter={8}>
            <Col span={8}>
              {getFieldDecorator('captcha', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input size="large" style={{left:-12}} />
              )}
            </Col>
            <Col span={8}>
              <img src={this.state.captchaUrl} title='' onClick={this.changeCaptcha} style={{height:'32px'}} />
            </Col>
          </Row>
        </FormItem>
        <FormItem {...tailFormItemLayout} style={{ marginBottom: 8 }}>
          {getFieldDecorator('agreement', {
            valuePropName: 'checked',
          })(
            <Checkbox> <a></a></Checkbox>
          )}
        </FormItem>
        <FormItem {...tailFormItemLayout}>
            <Row gutter={8} justify="center" >
                <Col span={8} push={4}>
                    <Button type="primary" htmlType="submit" disabled={checkformSubmit(this.props.form)} size="large"></Button>   
                </Col>
                <Col span={8}  >
                    <Button size="large" onClick={this.formReset} ></Button>
                </Col>
                  <Col span={8} pull={4}  >
                    <Button size="large" onClick={this.setFormData} ></Button>
                </Col>
            </Row>
        </FormItem>
      </Form>
    </div>
  }
}



export default Form.create()(RegistrationForm);


